.card-pc .additional-pc .more-info {
  width: 300px;
  float: left;
  position: absolute;
  left: 150px;
  height: 100%;
}

.card-pc .additional-pc .more-info h1 {
  color: #fff;
  margin-bottom: 0;
}

.card-pc.green .additional-pc .more-info h1 {
  color: #224C36;
}

.card-pc .additional-pc .coords {
  margin: .5rem 2rem;
  color: #fff;
  font-size: 1rem;
}

.card-pc.green .additional-pc .coords {
  color: #325C46;
}

.card-pc .additional-pc .coords span + span {
  float: right;
}

.card-pc .additional-pc .stats {
  font-size: 2rem;
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  right: 1rem;
  top: auto;
  color: #fff;
}

.card-pc.green .additional-pc .stats {
  color: #325C46;
}

.card-pc .additional-pc .stats > div {
  flex: 1 1;
  text-align: center;
}

.card-pc .additional-pc .stats i {
  display: block;
}

.card-pc .additional-pc .stats div.title {
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
}

.card-pc .additional-pc .stats div.value {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.5rem;
}

.card-pc .additional-pc .stats div.value.infinity {
  font-size: 2.5rem;
}

.card-pc .general-pc {
  width: 300px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 100px;
  box-sizing: border-box;
  padding-top: 0;
}

.card-pc .general-pc .more {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  font-size: 0.9em;
}



.auth-container{
    background-color: #F5A503 ;
    background-image: url(/static/media/dog-background.ba93e1dc.png);
    background-repeat: no-repeat;
    background-position: 5%;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.body-form {
    background: #f6f5f7;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 180vh;
}

.h1-form {
    font-weight: bold;
	margin-top: 25px;
    margin-right: 95px;
}

.p-form {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: .5px;
    margin: 20px 0 30px;
}

.a-form {
    color: #333;
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
}

.container-form {
    background: #fff;
    border-radius: 30px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, .2), 0 10px 10px rgba(0, 0, 0, .2);
    position: relative;
    overflow: hidden;
    width: 550px;
    min-height: 600px;
}

.form-container {
    background: #fff;
    display: flex;
    flex-direction: column;
    padding:  10px 0 50px;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
}


.form-container input {
    background: #eee;
    border: none;
    padding: 12px 15px;
    margin: 20px 0;
    width: 100%;
}

button:active {
    -webkit-transform: scale(.95);
            transform: scale(.95);
}

.form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all .6s ease-in-out;
}

.sign-in-container {
    margin-left: 140px;
}

.sign-up-container {
    left: 0;
    width: 50%;
    z-index: 1;
    opacity: 0;
}

.span-container{
    margin-top: 20px;
}
.filter{
margin-left: 30%;
margin-right: 20%;
}

.container-petprofile{
    margin-left: 20%;
}

.pet-profile-cards{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
	align-items: center;
	align-content: space-around;
}

.btn-add-pet{
	background: #F5A503;
    color: #F9F8EA;
    padding: 10px 30px;
    margin-top: 30px;
    border-radius: 50px;
    text-decoration: none;
    display: inline-block;
    transition: 0.5s;
}
